import React, { useState, useEffect, useRef } from 'react';
import CollapseToggleButton from './CollapseToggleButton';
import icons from './icons';
import getKey from './KeyGenerator';
import Sounds from '../sound/Sounds';
import Dialog from '../functional/Dialog.js';

const StepTimer = () => {

    const timerState = JSON.parse(localStorage.getItem('timerState')) || {};
    const [steps, setSteps] = useState(JSON.parse(localStorage.getItem('steps')) || []);
    const [currentStepIndex, setCurrentStepIndex] = useState(timerState.currentStepIndex || 0);
    const [newHours, setNewHours] = useState('');
    const [newMinutes, setNewMinutes] = useState('');
    const [newSeconds, setNewSeconds] = useState('');
    const [currentTimer, setCurrentTimer] = useState(Number(timerState.currentTimer) || 0);
    const [isRunning, setIsRunning] = useState(timerState.isRunning || false);
    const [newStep, setNewStep] = useState('');
    const [newTimer, setNewTimer] = useState('');
    const [timeLeft, setTimeLeft] = useState(timerState.timeLeft || 0);
    const timerRef = useRef(null);
    const intervalRef = useRef(null);
    const [stepCollapse, setStepCollapse] = useState(true);
    const [stepsCollapse, setStepsCollapse] = useState(true);
    const [dialog, setDialog] = useState('')

    const totalSeconds =
        parseInt(newHours || 0) * 3600 +
        parseInt(newMinutes || 0) * 60 +
        parseInt(newSeconds || 0);

    const handleAddStep = () => {

        if (newStep && newTimer > 0) {
            setSteps([...steps, { step: newStep, timer: parseInt(newTimer) }]);
            setNewStep('');
            setNewTimer('');
        }

        const totalSeconds =
            parseInt(newHours || 0) * 3600 +
            parseInt(newMinutes || 0) * 60 +
            parseInt(newSeconds || 0);

        if (newStep && totalSeconds > 0) {
            setSteps([...steps, { step: newStep, timer: totalSeconds }]);
            setNewStep("");
            setNewHours("");
            setNewMinutes("");
            setNewSeconds("");
        }
    };

    const handleDeleteStep = (id) => {
        const newSteps = [...steps];
        newSteps.splice(id, 1);
        setSteps(newSteps);
    };

    const handleStartSequence = () => {
        if (steps.length > 0) {
            setCurrentStepIndex(0);
            setCurrentTimer(steps[0].timer);
            setIsRunning(true);
        }
    };
    
    const handleRestartStep = () => {
        if (steps.length > 0) {
            setCurrentTimer(steps[currentStepIndex].timer);
            setIsRunning(true);
        }
    };

    const toggleTimer = () => {
        setIsRunning((prev) => !prev);
    };

    const startTimer = () => {
        if (intervalRef.current) return; // Prevent multiple intervals
        setIsRunning(true);
        intervalRef.current = setInterval(() => {
            setTimeLeft((prev) => {
                if (prev <= 1) {
                    clearInterval(intervalRef.current);
                    intervalRef.current = null;
                    moveToNextStep();
                    return 0;
                }
                return prev - 1;
            });
        }, 1000);
    };

    const pauseTimer = () => {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
        setIsRunning(false);
    };

    const moveToNextStep = () => {
        const nextIndex = currentStepIndex + 1;
        if (nextIndex < steps.length) {
            setCurrentStepIndex(nextIndex);
            setTimeLeft(steps[nextIndex].timer);
            startTimer();
        } else {
            setIsRunning(false);
        }
    };

    useEffect(() => {
        if (isRunning && currentTimer > 0) {
            timerRef.current = setTimeout(() => {
                setCurrentTimer((prev) => prev - 1);
            }, 1000);
        } else if (isRunning && currentTimer === 0) {
            clearTimeout(timerRef.current);
            handleNextStep();
        }
        return () => clearTimeout(timerRef.current);
    }, [isRunning, currentTimer]);

    useEffect(() => {
        // Persist timer state to localStorage
        console.log(`isRunning: ${isRunning}`);
        localStorage.setItem('timerState', JSON.stringify({
                timeLeft,
                isRunning,
                lastTimestamp: Date.now(),
                currentTimer,
                currentStepIndex
            })
        );
        localStorage.setItem('steps', JSON.stringify(steps));
        localStorage.setItem('currentStepIndex', currentStepIndex);
    }, [timeLeft, isRunning, steps, currentStepIndex, currentTimer]);

    useEffect(() => {
        const savedState = timerState;

        if (savedState) {

            const { 
                lastTimestamp, 
                timeLeft: savedTimeLeft, 
                isRunning: wasRunning, 
                currentTimer: savedCurrentTimer, 
                currentStepIndex: savedCurrentStepIndex 
            } = savedState;

            const elapsed = Math.floor((Date.now() - lastTimestamp) / 1000);

            if (wasRunning) {
                const updatedTimeLeft = Math.max(savedTimeLeft - elapsed, 0);
                setTimeLeft(updatedTimeLeft);
                setCurrentTimer(Math.max(savedCurrentTimer - elapsed, 0));
                setCurrentStepIndex(savedCurrentStepIndex);

                if (updatedTimeLeft > 0) {
                    startTimer();
                } else {
                    moveToNextStep();
                }
            } else {
                setTimeLeft(savedTimeLeft);
                setCurrentTimer(savedCurrentTimer);
                setCurrentStepIndex(savedCurrentStepIndex);
            }
            setIsRunning(wasRunning);
        } else if (steps[currentStepIndex]) {
            setTimeLeft(steps[currentStepIndex].timer);
            setCurrentTimer(steps[currentStepIndex].timer);
        }
    }, []);

    useEffect(() => {
        localStorage.setItem('steps', JSON.stringify(steps));
    }, [steps]);

    useEffect(() => {
        const totalSeconds =
            parseInt(newHours || 0) * 3600 +
            parseInt(newMinutes || 0) * 60 +
            parseInt(newSeconds || 0);

        /* if (newStep && totalSeconds > 0) {
            setSteps([...steps, { step: newStep, timer: totalSeconds }]);
            setNewStep("");
            setNewHours("");
            setNewMinutes("");
            setNewSeconds("");
        } */
    }, [newHours, newMinutes, newSeconds]);

    const formatTime = (seconds) => {
        const hrs = Math.floor(seconds / 3600);
        const mins = Math.floor((seconds % 3600) / 60);
        const secs = seconds % 60;
        return `${hrs.toString().padStart(2, "0")}:${mins
            .toString()
            .padStart(2, "0")}:${secs.toString().padStart(2, "0")}`;
    };
    
    const getHours = (totalSeconds) => Math.floor(totalSeconds / 3600);
    const getMinutes = (totalSeconds) =>Math.floor((totalSeconds % 3600) / 60);
    const getSeconds = (totalSeconds) => totalSeconds % 60;

    const handleNextStep = () => {
        const nextStepIndex = currentStepIndex + 1;
        if (nextStepIndex < steps.length) {
            setCurrentStepIndex(nextStepIndex);
            setCurrentTimer(steps[nextStepIndex].timer);
        } else {
            setIsRunning(false);
            setCurrentStepIndex(-1);
        }
    };
    const handlePreviousStep = () => {
        const previousStepIndex = currentStepIndex - 1;
        if (previousStepIndex > -1) {
            setCurrentStepIndex(previousStepIndex);
            setCurrentTimer(steps[previousStepIndex].timer);
        } else {
            setIsRunning(false);
            setCurrentStepIndex(-1);
        }
    };

    const handleSaveSteps = () => {
        localStorage.setItem('steps', JSON.stringify(steps));
        alert('Steps saved successfully!');
    };

    useEffect(() => {
        if (currentTimer === 0 && isRunning) {
            Sounds['boop'](5);
        }
    }, [currentTimer, isRunning]);

    const getSteps = () => {
        const displaySteps = steps.length > 0 && (
            <div className='containerBox pt-15 bg-lite'>
                <span className='button size30 mr-15' title='previous step' onClick={handlePreviousStep}>{icons.up}</span>
                <span className='button size30 mr-15' title='reset timer' onClick={handleRestartStep}>{icons.start}</span>
                <span className='size30 mr-15' title='restart steps' onClick={handleStartSequence}>{icons.restart}</span>
                <span className='size30 mr-15' title={isRunning ? 'pause' : 'play'}  onClick={toggleTimer}>{isRunning ? icons.pause : icons.play }</span>
                <span className='button size30 mr-15' title='next step' onClick={handleNextStep}>{icons.end}</span>
                <span className='button size30' title='next step' onClick={handleNextStep}>{icons.down}</span>
            </div>
        )
        return displaySteps;
    }
    const getEditDialog = (id) => {
        const newDialog = <div className='containerBox'>
            <input
                type='text'
                placeholder='Step Description'
                value={newStep}
                onChange={(e) => setNewStep(e.target.value)}
                className='containerDetail width-100-percent'
            />
            <br />
            <input
                type='number'
                placeholder='Hours'
                value={newHours}
                onChange={(e) => setNewHours(e.target.value)}
            />
            <input
                type='number'
                placeholder='Minutes'
                value={newMinutes}
                onChange={(e) => setNewMinutes(e.target.value)}
            />
            <input
                type='number'
                placeholder='Seconds'
                value={newSeconds}
                onChange={(e) => setNewSeconds(e.target.value)}
            />
            <button
                onClick={() => setDialog('')}
                className='containerBox'
            >
                SUBMIT
            </button>
            <button
                onClick={() => setDialog('')}
                className='containerBox'
            >
                CANCEL
            </button>
        </div>
        setNewStep(steps[id].step);
        setNewHours(getHours(steps[id].timer));
        setNewMinutes(getMinutes(steps[id].timer));
        setNewSeconds(getSeconds(steps[id].timer));
        setNewTimer(steps[id].timer)
        setDialog(newDialog);

    }

    const editStep = (index) => {
        const newStep = prompt(`Edit step:`, steps[index].step);
        const newSteps = [...steps];
        newSteps[index].step = newStep;
        setSteps(newSteps);
    }
    const editTimer = (index) => {
        //const newTimer = prompt(`Edit timer (${formatTime(steps[index].timer)}):`, steps[index].timer);
        const newHours = prompt(`Edit hours:`, getHours(steps[index].timer));
        const newMinutes = prompt(`Edit minutes:`, getMinutes(steps[index].timer));
        const newSeconds = prompt(`Edit seconds:`, getSeconds(steps[index].timer));
        const totalSeconds =
            parseInt(newHours || 0) * 3600 +
            parseInt(newMinutes || 0) * 60 +
            parseInt(newSeconds || 0);
        const newSteps = [...steps];
        newSteps[index].timer = totalSeconds;
        setSteps(newSteps);
    }
    const editSteps = (index) => {
        editStep(index);
        editTimer(index);
    }

    return (
        <div className='containerBox'>
            {/* <div className='containerBox'>
                {dialog}
            </div> */}
            <div className='containerBox'>
                    <div className='containerDetail '>
                    <div className='containerDetail color-yellow size45 bold p-20 bg-tintedMedium m-5'>{steps[currentStepIndex]?.step}</div>
                        <div className='containerDetail size35 bg-dark p-20 ml-5 mr-5'>{formatTime(currentTimer)}</div>
                        {getSteps()}
                    </div>
            </div>
            <div className={`containerDetail color-yellow size25 bold`}>
                <CollapseToggleButton
                    title={`Add Steps`}
                    isCollapsed={stepCollapse}
                    setCollapse={setStepCollapse}
                    align='left'
                />
            </div>
            {
                (stepCollapse)
                    ? null
                    : <div className='containerBox'>
                        <input
                            type='text'
                            placeholder='Description'
                            value={newStep}
                            onChange={(e) => setNewStep(e.target.value)}
                            className='containerBox width-100-percent'
                        />
                        <br />
                        {/*
                    <input
                        type='number'
                        placeholder='Timer (seconds)'
                        value={newTimer}
                        onChange={(e) => setNewTimer(e.target.value)}
                        className='containerBox bold width-50-percent'
                    />
                    */}
                        <input
                            type='number'
                            placeholder='Hours'
                            value={newHours}
                            onChange={(e) => setNewHours(e.target.value)}
                            className='containerBox width-100-percent'
                        />
                        <input
                            type='number'
                            placeholder='Minutes'
                            value={newMinutes}
                            onChange={(e) => setNewMinutes(e.target.value)}
                            className='containerBox width-100-percent'
                        />
                        <input
                            type='number'
                            placeholder='Seconds'
                            value={newSeconds}
                            onChange={(e) => setNewSeconds(e.target.value)}
                            className='containerBox width-100-percent'
                        />
                        <br />
                        <button className='containerBox button color-lite width-100-percent bg-green' onClick={handleAddStep}>Add Step</button>
                    </div>
            }
            <div className={`containerDetail color-yellow size25 bold`}>
                <CollapseToggleButton
                    title={`Display Steps`}
                    isCollapsed={stepsCollapse}
                    setCollapse={setStepsCollapse}
                    align='left'
                />
            </div>
            {
                (stepsCollapse)
                    ? null
                    : <div className='containerBox contentCenter'>
                        <div className='containerDetail sides-auto'>
                            {steps.map((step, index) => (
                                <div key={index} className='flexContainer'>
                                    <div key={index} className='containerBox flex2Column contentLeft'> 
                                        <div className='containerBox color-yellow bold' onClick={() => editStep(index)}>{index + 1}. {step.step}</div>
                                        <div className='containerBox bg-lite contentCenter' onClick={() => editTimer(index)}>{formatTime(step.timer)}</div>
                                    </div>
                                    <div className='containerBox flexColumn flexContainer'>
                                        <div className='button flex2Column containerBox columnCenterAlign' onClick={() => editSteps(index)}>
                                            {icons.edit}
                                        </div>
                                        <div className='button flex2Column containerBox columnCenterAlign' onClick={() => handleDeleteStep(index)}>
                                            {icons.delete}
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                        {steps.length > 0 && <button className='containerBox button color-lite' onClick={handleSaveSteps}>Save Steps</button>}
                    </div>
            }
        </div>
    );
};

export default StepTimer;